<template>
    <div class="main">

        <!-- 二级路由插座 -->

        <div>
            <router-view></router-view>
        </div>

        <van-tabbar v-model="active" route active-color="#842600" inactive-color="#bfbfbf">
              <van-tabbar-item  replace :to="item.path" :icon="item.icon" v-for="(item,index) in tabArr" :key="index">
                  

                  <span>{{item.title}}</span>
                  <template #icon="props">
                    <img :src="props.active ? item.activeImg : item.inactiveImg" />
                  </template>
                  
                  </van-tabbar-item>
             
        </van-tabbar>

    </div>
</template>

<script>
    export default {
        name:'Main',
        data(){
            return{

                active:0,

                tabArr:[
                    {
                        title:'首页',
                        icon:'home-o',
                        path:'/main/home',
                        inactiveImg:require('../assets/images/firstpage.png'),
                        activeImg:require('../assets/images/firstpage_active.png'),
                    },
                    {
                        title:'菜单',
                        icon:'search',
                        path:'/main/menu',
                        inactiveImg:require('../assets/images/product.png'),
                        activeImg:require('../assets/images/product_active.png'),
                    },
                    {
                        title:'购物袋',
                        icon:'friends-o',
                        path:'/main/shopbag',
                        inactiveImg:require('../assets/images/shopbag.png'),
                        activeImg:require('../assets/images/shopbag_active.png'),
                    },
                    {
                        title:'我的',
                        icon:'setting-o',
                        path:'/main/mine',
                        inactiveImg:require('../assets/images/mine.png'),
                        activeImg:require('../assets/images/mine_active.png'),
                    },
                ]

            }
        }
        
    }
</script>

<style lang="less" scoped>

 .van-tabbar,.van-tabbar-item--active /deep/{
 
 background-color: #fcf1ee;
}



